@import "/src/app/styles/variables";

// All lessons box

.custom_select {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.btn_custom_select {
  background: none;
  border: none;
  cursor: pointer;
}

.lessons_container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

