// 	grid-template-columns: repeat(4, 1fr);
// 	padding-top: 2%;
// 	gap: 20px;
// 	width: 100%;
// }
.all_lessons_box {
  display: flex;
  flex-wrap: wrap;
  padding-top: 2%;
  gap: 20px;
  //   padding: 0px 40px;
  justify-content: center;
  max-width: 1920px;
  width: 100%;
}

// Search and filter
// Filter
