    width: 300px;
  }

  .img_lesson {
    width: 300px;
  }

  .info {
    width: 300px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  .img_channel {
    width: 30px;
  }

  .name_channel {
    margin: 0;
    font-size: 0.8rem;
  }

  .view_comm_box {
    gap: 10px;
  }

  .view_comment {
    font-size: 0.7rem;
  }

  .name_view_com {
    gap: 7px;
  }

  .buy_btn {
    width: 90px;
    height: 30px;
    padding: 10px;
  }

  .channel_box {
    display: flex;
    margin-top: 10px;
    gap: 50px;
    align-items: center;
    justify-content: center;
  }

  .num_buy {
    font-size: 0.7rem;
  }

  .time_lessons {
    font-size: 0.7rem;
    top: 145px;
    left: 310px;
  }

  .button_play {
    top: 70px;
    left: 180px;
  }

  .text_buy {
    font-size: 0.6rem;
  }
  .num_buy {
    font-size: 0.6rem;
  }

  .img_buy {
    width: 17px;
  }
}

