@import "/src/app/styles/variables";
* {
  left: 0;
  top: 0;
}

// ProfileCard

.profile_box {
  display: flex;
  flex-direction: column;
  position: absolute;
  max-width: 410px;
  height: auto;
  width: 100%;
  margin-top: 111px;
  margin-left: 5%;
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 2px 4px 10px 0px rgba(25, 18, 129, 0.25);
}

.box_name_mobile {
  display: none;
}

.img_me {
  width: 100%;
  height: 433px;
  border-radius: 20px 20px 0px 0px;
}

.box_loc_vid_view {
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
  margin-left: 22px;
}

.location {
  display: flex;
  align-items: center;
  flex-direction: row;
  gap: 10px;
  font-family: $primary_font;
  font-size: 1.4rem;
  font-weight: 500;
}

.videos_view {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 40px;
  max-height: 24px;
  margin-bottom: 30px;
}

.vid_see {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-size: 1.4rem;
  font-family: $secondary_font;
  font-weight: 400;
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
}

.button {
  border-radius: 25px;
  box-shadow: 1px 2px 25px 0px rgba(20, 47, 136, 0.25);
  border: none;
  padding: 10px 65px;
  width: 90%;
  background-color: rgba(255, 255, 255, 0.8);
}

.btn_text {
  font-family: $primary_font;
  font-size: 1.2rem;
  background-size: 100%;
  background-image: $gradient_color;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.active_button {
  background-image: $gradient_color;
}

.btn_text_active {
  background-image: none;
  -webkit-text-fill-color: $primary_color;
}

.info_me {
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
  margin-left: 22px;
}

.title_me {
  font-family: $primary_font;
  color: rgba(0, 0, 0, 1);
  font-weight: 600;
  font-size: 1.4rem;
  height: 10px;
}

.text_me {
  font-family: $primary_font;
  color: rgba(0, 0, 0, 1);
  font-weight: 400;
  font-size: 1.1rem;
}

// Profile_all_container

.profile_all_container {
  position: absolute;
}

.profile_top_img {
  width: 100%;
}

.username_box {
  text-align: start;
  position: absolute;
  margin-top: 2%;
  margin-left: 29%;
}

.username {
  font-family: $secondary_font;
  font-size: 8rem;
  font-weight: 700;
  height: 10px;
  color: rgba(255, 255, 255, 1);
}

.buttons_page_profile {
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 15px;
  margin-left: 555px;
  margin-top: 42px;
}

.buttons_containers_profile {
  display: flex;
  flex-direction: row;
  gap: 15px;
}

.button_main_profile {
  border: none;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 1px 2px 25px 0px rgba(20, 47, 136, 0.25);
  padding: 10px 30px;
  border-radius: 20px;
}

.button_main_profile_text {
  font-family: $primary_font;
  font-size: 1.4rem;
  background-size: 100%;
  background-image: $gradient_color;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
// Numbers
.sup {
  font-size: 0.625rem;
}

@media (width <= 1800px) {
  .username_box {
    margin-left: 31%;
  }
  .username {
    font-size: 7.5rem;
  }
}

@media (width <= 1600px) {
  .profile_box {
    width: 380px;
  }

  .img_me {
    height: 359px;
  }

  .username_box {
    margin-left: 32%;
  }
  .username {
    font-size: 6rem;
  }

  .button_main_profile_text {
    font-size: 1.2rem;
  }
}

@media (width <= 1440px) {
  .profile_box {
    width: 330px;
  }
  .img_me {
    height: 325px;
  }
  .username_box {
    margin-left: 440px;
  }

  .username {
    font-size: 6rem;
  }

  .location {
    font-size: 1.1rem;
  }

  .location_img {
    width: 12%;
  }

  .vid_see {
    font-size: 1.1rem;
  }

  .vid_see_img {
    width: 45%;
  }

  .btn_text {
    font-size: 0.9rem;
  }

  .title_me {
    font-size: 1.2rem;
  }
  .text_me {
    font-size: 1rem;
  }

  .buttons_page_profile {
    margin-left: 440px;
  }

  .button_main_profile_text {
    font-size: 1.1rem;
  }
}

@media (width <= 1024px) {


  .img_me {
    height: 294px;
  }
  .username {
    font-size: 5rem;
  }
}

// Mobile version

@media (width <= 425px) {
  .profile_all_container {
    position: inline;
    display: flex;
    flex-direction: column;
  }

  .username_box {
    display: none;
  }

  .profile_box {
    background: none;
    box-shadow: none;
    align-items: center;
    width: 100%;
    max-width: 425px;
    margin-left: 0;
    margin-top: 130px;
  }

  .img_me {
    width: 210px;
    height: 210px;
    border-radius: 150px;
  }

  .box_name_mobile {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 28px;
  }

  .username_mobile {
    background-image: $gradient_color;
    background-size: 100%;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: $secondary_font;
    font-weight: 700;
    font-size: 1.25rem;
  }

  .profile_img_box {
    background-color: white;
    border-radius: 150px;
    padding: 5px;
  }

  .box_loc_vid_view {
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 20px;
  }

  .videos_view {
    margin: 0;
    gap: 20px;
    font-size: 0.8rem;
  }

  .vid_see {
    font-size: 0.8rem;
  }

  .location {
    font-size: 0.8rem;
  }

  .button {
    width: 400px;
  }

  .buttons {
    gap: 8px;
  }

  .info_me {
    text-align: left;
    width: 400px;
    padding-bottom: 80px;
  }

  .title_me {
    font-size: 1rem;
    height: 2px;
  }

  .text_me {
    font-size: 0.75rem;
  }

  .buttons_page_profile {
    display: none;
  }

  .profile_top_img {
    content: url("../../app/assets/profileCard/phone_img_bg.svg");
    // height: 258px;
  }
}

@media (width <= 375px) {
  .profile_top_img {
    height: auto;
  }

  .profile_all_container {
    max-width: 375px;
    padding-bottom: 20px;
  }

  .profile_box {
    max-width: 375px;
  }

  .img_me {
    width: 190px;
    height: 190px;
  }

  .button {
    width: 350px;
  }

  .info_me {
    max-width: 230px;
    width: 100%;
    padding-left: 0px;
  }
}

@media (width <= 325px) {
  .profile_top_img {
    height: 100%;
  }

  .profile_all_container {
    max-width: 325px;
    padding-bottom: 20px;
  }

  .profile_box {
    max-width: 325px;
  }

  .img_me {
    width: 150px;
    height: 150px;
  }

  .button {
    width: 290px;
    padding: 5px 65px;
  }

  .info_me {
    width: 90%;
    max-width: 250px;
  }

  .btn_text {
    font-size: 0.8rem;
  }
}
